.socials {
  display: flex;
  margin-top: 0;
  margin-bottom: 0;

  &__item {
    &:not(:last-of-type) {
      margin-right: 1rem;
    }

    &-icon {
      padding: 2rem;

      @include md {
        transition: opacity 300ms ease-in-out;
        opacity: 0.7;

        &:hover {
          opacity: 1;
        }
      }
    }
  }
}
