#footer {
  .footer {
    &__links {
      display: flex;
      flex-direction: column;
      margin-bottom: 2rem;

      @include md {
        align-items: center;
        justify-content: space-between;
        flex-direction: row;
      }

      &-email {
        margin-bottom: 2rem;

        @include md {
          margin-bottom: 0;
        }
      }
    }
  }
}
