*::selection {
  background: $pink;
}

html,
body {
  height: 100%;
}

body {
  width: 100%;
  min-height: 100%;
  margin: 0;
}

.wrapper {
  max-width: 100%;
}

.content {
  width: 100%;
  max-width: $screen-md-min;
  margin: 0 auto;
  text-align: left;
}

img {
  display: block;
}

ul {
  padding: 0;
  list-style: none;
}
