section {
  display: flex;
  align-items: center;
  min-height: 50%;
  padding: 5rem 2.5rem;

  @include md {
    padding: 15rem 10rem;
  }

  &:not(:last-child) {
    border-bottom: 1px solid $grey;
  }

  &:nth-child(2n) {
    background-color: $light-grey;
    box-shadow: inset 0 1px 0 0 $white;
  }
}
