@font-face {
  font-family: 'Lora';
  src: url('fonts/Lora-Italic.eot');
  src: local('Lora Italic'), local('Lora-Italic'),
    url('fonts/Lora-Italic.eot?#iefix') format('embedded-opentype'),
    url('fonts/Lora-Italic.woff2') format('woff2'),
    url('fonts/Lora-Italic.woff') format('woff'),
    url('fonts/Lora-Italic.ttf') format('truetype');
  font-weight: normal;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: 'Lora';
  src: url('fonts/Lora-BoldItalic.eot');
  src: local('Lora Bold Italic'), local('Lora-BoldItalic'),
    url('fonts/Lora-BoldItalic.eot?#iefix') format('embedded-opentype'),
    url('fonts/Lora-BoldItalic.woff2') format('woff2'),
    url('fonts/Lora-BoldItalic.woff') format('woff'),
    url('fonts/Lora-BoldItalic.ttf') format('truetype');
  font-weight: bold;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: 'Lora';
  src: url('fonts/Lora-Bold.eot');
  src: local('Lora Bold'), local('Lora-Bold'),
    url('fonts/Lora-Bold.eot?#iefix') format('embedded-opentype'),
    url('fonts/Lora-Bold.woff2') format('woff2'),
    url('fonts/Lora-Bold.woff') format('woff'),
    url('fonts/Lora-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Lora';
  src: url('fonts/Lora-Regular.eot');
  src: local('Lora Regular'), local('Lora-Regular'),
    url('fonts/Lora-Regular.eot?#iefix') format('embedded-opentype'),
    url('fonts/Lora-Regular.woff2') format('woff2'),
    url('fonts/Lora-Regular.woff') format('woff'),
    url('fonts/Lora-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

html {
  font-size: 62.5%; // 1rem = 10px, 3.5rem = 35px;
  font-family: 'Lora', 'Times New Roman', Times, serif;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
  color: $text-primary;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  margin: 0;
}

h1 {
  font-size: 3.2rem;
  font-weight: bold;
  margin-bottom: 0.7rem;

  @include md {
    font-size: 4.2rem;
  }
}

h2 {
  font-size: 2.8rem;
  margin-bottom: 2.5rem;

  @include md {
    font-size: 3.2rem;
  }
}

h6 {
  font-size: 1.8rem;
}

p {
  font-size: 1.8rem;
  font-weight: 400;
  margin: 0;
}

a {
  text-decoration: none;
  color: $blue;
}
