#experience {
  .list {
    padding: 0 2rem;
    list-style-type: disc;

    &__item {
      margin-top: 3.5rem;
      margin-bottom: 3.5rem;

      &-name {
        font-size: 2rem;
      }

      &-duration {
        font-size: 1.6rem;
        margin-top: 0.75rem;
        margin-bottom: 0.75rem;
      }

      &-position {
        font-size: 1.6rem;
        color: $text-secondary;
      }
    }
  }
}
