$icons: (
  "github",
  "telegram",
  "themost",
  "karofilm",
  "schedule",
  "schedule-admin",
  "newprovidence",
  "mmio",
  "battlespace",
  "vk",
  "blueasy",
  "minimo",
  "slider",
  "player",
  "pictureperfect",
  "signature",
  "asiamix",
  "reduction",
  "linkedin"
);

.icon {
  display: block;
  padding: 3rem;
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;

  @include sm {
    padding: 4rem;
  }

  @include md {
    padding: 3rem;
  }

  // SVG only
  @each $icon in $icons {
    &-#{$icon} {
      background-image: url("images/#{$icon}.svg");
    }
  }
}
